$primary-color: #3D3D3D;
$bg-color: #EBEBEB;

@mixin btn {
  display: block;
  text-decoration: none;
  text-align: center;
  font-size: 32px;
  font-weight: 300;
  border: 1px solid $primary-color;
  width: 310px;
  padding: 20px 0;
  color: $primary-color;
}

* {
  margin: 0;
  padding: 0;
}

html {
  font-family: 'Open Sans', sans-serif;
  color: #3D3D3D;
}

// .container {
//   //display: flex;
// //  width: 1055px;
// //  margin: 0 auto;
// }

.loginList {
  margin-top: 30px;
  display: flex;
  justify-content: flex-end;
  //justify-content: space-between;
  list-style: none;
    li {
      margin-left: 20px;
    }

}

.navMenu {
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
  list-style: none;
   li {
     //margin-left: 20px;
     padding: 10px 30px;
   }
   li:first-child {
     border: 1px solid black;
   }
}

//header {
//  background-image: url("../img/header_start_img.jpg");
//  background-repeat: no-repeat;
//  height: 1000px;
//}
header {
  display: flex;
  justify-content: space-between;
}

.headerLeft {
    background-image: url("../img/header_start_img.jpg");
    background-repeat: no-repeat;
    height: 1000px;
    width: 55%;
}

.headerRight {
  width: 45%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  text-align: center;
   h1 {
     font-weight: 300;
     //font-size: 32px;
   }
}

.headerRightImg {
  margin-top: 50px;
  margin-bottom: 100px;
}

.headerBtns {
  display: flex;
  justify-content: space-around;
  width: 100%;
    a {
      @include btn();
    }
}

.yellowBar {
  height: 345px;
  //width: 100%;
  display: flex;
  justify-content: space-around;
  background-image: url("../img/yellowbg.png");
  background-repeat: no-repeat;
   .column1,.column2,.column3 {
     width: 25%;
     text-align: center;
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
   }
}

.givenItemsNr,.organizationNr,.givawaysNr {
  font-size: 75px;
  font-weight: 300;
}

.colTitle {
  margin: 15px 0;
}

.container {
  width: 1182px;
  height: 700px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
   h2 {
     font-weight: 400;
     font-size: 35px;
   }
   a {
     @include btn();
   }
}

.specialsImg {
  margin-top: 30px;
}

.specialsIcons {
  width: 100%;
  height: 330px;
  background: $bg-color;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 30px 0;
   .specialsCol {
     text-align: center;
     width: 18%;
     hr {
       display: block;
       border: 1px solid $primary-color;
       width: 50%;
       //height: 1px;
       background-color: $primary-color;
       margin-left: auto;
       margin-right: auto;
     }
     img {
       margin-bottom: 8px;
     }
     p {
       margin: 12px 0;
     }
   }
}

.aboutUs {
  background-color: $bg-color;
  width: 100%;
  height: 825px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.aboutUsTxt {
  //width: 25%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  h2 {
    font-size: 35px;
    font-weight: 400;
    margin: 20px 0;
  }
  p {
    margin: 20px 0;
    font-size: 25px;
    width: 50%;
    line-height: 40px;
  }
  .aboutUsSign {
    //margin-top: 20px;
    width: 60%;
    display: flex;
    justify-content: flex-end;
  }
}

.aboutUsImg {
  height: 100%;
  //width: 70%;
  display: flex;
  align-items: center;
  img {
    height: 90%;
  }
}

.menu {
  width: 100%;
  height: 400px;
  display: flex;
  justify-content: center;
}

.menuContainer {
  margin-top: 40px;
  width: 600px;
  .menuHeader {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    h2 {
      margin: 20px 0;
      font-weight: normal;
    }
  }
}

.menuList {
  margin: 20px 0;
  ul {
    display: flex;
    align-items: center;
    text-decoration: none;
    list-style: none;
    li:first-child {
      border: 1px solid $primary-color;
      width: 200px;
      padding: 20px 0;
      text-align: center;
      //margin: 0 auto;
    }
    li {
      padding: 20px;
    }
  }
}

.carousel {
  width: 100%;
  display: flex;
  justify-content: center;
  //align-items: center;
}

.menuCarousel {
  width: 1200px;
  //height: 800px;
}

.carouselList {
  width: 100%;
  //height: 100%;
  list-style: none;
    li {
      width: 100%;
      font-size: 22px;
      margin: 40px 0;
    }
}

.smallLetters {
  font-size: 16px;
  font-style: italic;
}

.carouselItem {
  display: flex;
  justify-content: space-between;
}

.horizontalLine {
  border: 0.5px solid #3D3D3D;
  width: 500px;
  margin-top: 32px;
}

.carouselNr {
  display: flex;
  justify-content: center;
  margin-bottom: 90px;
    span {
      border: 1px solid black;
      padding: 8px 12px;
      margin: 0 8px;
    }
}

.contactUs {
  height: 860px;
  background: #EFEFEF;
  background-image: url("../img/footer_bg_img.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.contactForm {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.contactFormLeft {
  width: 45%;
}